import { isEmpty } from './utils';

export default function placeholder(config) {
  const { locale } = config;
  // 可以通过先后顺序控制
  return [
    {
      text: locale?.taskListPlaceholder,
      when: (controller, node) => {
        return node?.data?.list?.isTaskList && isEmpty(node);
      },
    },
    {
      text: locale?.listPlaceholder,
      needWidth: true,
      when: (controller, node) => {
        return node?.data?.list && !node?.data?.list?.isTaskList && isEmpty(node);
      },
    },
  ];
}
